@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Montserrat', sans-serif;
  font-weight: bold;
}

h1 {
  margin-bottom: 20px;
  letter-spacing: 0.3rem;
}

.box {
  cursor: pointer;
  position: relative;
  height: 100px;
  width: 100px;
  border: 1px solid #000;
  border-radius: 50%;
  background-color: yellow;
  margin-bottom: 30px;
  animation: bounce 1.5s ease-in-out 0.5s infinite;
}

@keyframes bounce {
  from {
    transform: translateY(0%);
  }

  to {
    transform: translateY(100%);
  }
}

.container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 60vh;
  max-width: 800px;
  margin: 30px auto;
}

.colors {
  background-color: red !important;
}

.eyes-cont {
  display: flex;
  justify-content: space-between;
  width: 50px;
  position: absolute;
  top: 30px;
  left: 24px;
}

.eye {
  height: 22px;
  width: 15px;
  border-radius: 50%;
  background-color: #000;
}

.body-styles {
  background-color: rgb(90, 90, 255);
  color: #fff;
}

.instructions {
  position: fixed;
  bottom: 30px;
  right: 20%;
  text-align: center;
  font-size: 20px;
}
